import sleep from './sleep.js';

var controller = new AbortController();

export default function get(url, {retries=0, abort=false}={}) {
  // When abort is true, all previous outstanding requests will be aborted
  // before the cuurent request is made.
  if(abort) {
    controller.abort();
    controller = new AbortController();
  }
  console.log("=> get(", url, ", retries=", retries, ")");
  return new Promise(function(resolve, reject) {
    var signal = controller.signal;
    var response = fetch(url, {signal});
    console.log("<= get(", url, ", retries=", retries, ") ", response);
    response.then(result => {
      console.log("<= get(", url, ", retries=", retries, ") ", result);
      switch (result.status) {
        case 500:
        case 503:
          if (retries > 0) {
            sleep(1000).then(() => {
              get(url, {retries: retries - 1}).then(resolve, reject);
            });
          } else {
            resolve(result);
          }
          break;
        default:
          resolve(result);
      }
    }, (error) => {
      if(error.code === DOMException.ABORT_ERR) {
        // The request was aborted.
        console.log("Aborted: get(", url, ", retries=", retries, ")");
        return;
      }
      console.log("ERROR: get(", url, ", retries=", retries, ") ", error,
                   " errcode=", error.code, " errrmsg='", error.message, "'");
      if (retries > 0) {
        sleep(1000).then(() => {
          get(url, {retries: retries - 1}).then(resolve, reject);
        });
      } else {
        reject(error);
      }
    })
  });
}
