import React, { Component } from 'react';
import { Redirect } from 'react-router-dom'

import get from '../../utils/get.js';

class SummonerSearch extends Component {
  constructor(props) {
    super(props);

    this.defaultRegion = "na";
    this.state = {
      region: this.props.match.params.region || this.defaultRegion,
      summoner: this.props.match.params.summoner,
      props: {
        region: this.props.match.params.region,
        summoner: this.props.match.params.summoner
      },
      redirectToRegion: "",
      redirectToSummoner: "",
    }
  }

  componentDidUpdate() {
    console.log(this.props.name, "SummonerSearch componentDidUpdate()", "state:", this.state, "props:", this.props);
    this.setState((state) => {
      if (this.props.match.params.region !== this.state.props.region ||
          this.props.match.params.summoner !== this.state.props.summoner) {
        console.log(this.props.name, "SummonerSearch componentDidUpdate() region ->", this.props.match.params.region, ", summoner ->", this.props.match.params.summoner);
        return {
          props: {region: this.props.match.params.region, summoner: this.props.match.params.summoner},
          region: this.props.match.params.region || state.region || state.defaultRegion,
          summoner: this.props.match.params.summoner,
          redirectToRegion: "",
          redirectToSummoner: "",
        };
      }
    });
  };

  setRegion = event => {
    console.log(this.props.name, "SummonerSearch setRegion", this.state);
    console.log(this.state);
    console.log(event.target.dataset.region);
    this.setState({region: event.target.dataset.region});
  }

  setSummoner = event => {
    console.log(this.props.name, "SummonerSearch setSummoner", this.state);
    console.log(event.target.value);
    console.log(this.state);
    this.setState({summoner: event.target.value});
  }

  handleKeyDown = event => {
    console.log(event);
    console.log(event.target);
    event = event || window.event;
    if (event.keyCode === 13) {
      this.handleSearch();
      return false;
    }
  }

  handleSearch = () => {
    console.log(this.props.name, "SummonerSearch handleSearch", this.state);
    console.log(this.props.name, "handleSearch", this.state);
    if (this.state.region === this.props.match.params.region &&
        this.state.summoner === this.props.match.params.summoner) {
      this.props.refresh(this.state.region, this.state.summoner);
    } else {
      this.setState((state, props) =>
        ({ redirectToRegion: state.region, redirectToSummoner: state.summoner })
      );
    }
  }

  handleRandom = () => {
    console.log(this.props.name, "handleRandom", this.state);
    var url = "https://www.ocf.berkeley.edu/~omarali/lolapifalcon/" + this.state.region + "/randomlivesummoner";
    get(url, {abort: true}).then(result => {
      switch (result.status) {
        case 200:
          result.text().then(result => {
            this.setState({redirectToRegion: this.state.region, redirectToSummoner: result, error: ""});
          });
        break;
        default:
          result.text().then(error => {
            console.log(error);
          });
        break;
      }
    });
  }

  render() {
    console.log(this.props.name, "SummonerSearch render ", this.state);
    var region = this.state.region;

    if (this.state.redirectToRegion !== "" &&
        this.state.redirectToSummoner !== "" &&
        (this.state.redirectToRegion !== this.props.match.params.region ||
         this.state.redirectToSummoner !== this.props.match.params.summoner)) {
      return (<Redirect push to={"/" + this.state.redirectToRegion + "/" + this.state.redirectToSummoner} />)
    }

    var regions = ["na", "euw", "eune", "kr", "br", "tr", "ru", "lan", "las", "oce", "jp"];
    var regionList = regions.map((region) => {
      return <button class={"dropdown-item" + (this.state.region === region ? " active" : "")} type="button" onClick={this.setRegion} data-region={region}>{region.toUpperCase()}</button>;
    })

    return (
      <div class="form-inline">
        <div class="form-group w-100 justify-content-center">
          <div class={"input-group" + (this.props.large ? " input-group-lg" : "") + " w-75 shadow mr-2"}>
            <div class="input-group-prepend btn-group justify-content-center">
              <button class="btn btn-secondary dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">{region.toUpperCase()}</button>
              <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">{regionList}</div>
            </div>
            <SearchBox
              value={this.state.summoner}
              onChange={this.setSummoner}
              onKeyDown={this.handleKeyDown}
            />
            <div class="input-group-append">
              <SearchButton onClick={this.handleSearch} large={this.props.large} />
            </div>
          </div>
        <RandomButton onClick={this.handleRandom} large={this.props.large} />
        </div>
      </div>
    );
  }
}

class SearchBox extends Component {
  render() {
    return (
      <input
        id="summonerName"
        type="text"
        placeholder="Summoner Name"
        class="form-control"
        value={this.props.value}
        onChange={this.props.onChange}
        onKeyDown={this.props.onKeyDown}
      />
    );
  }
}

class SearchButton extends Component {
  render() {
    return (
      <button id="searchButton" type="button" class={"btn " + (this.props.large ? " btn-lg" : "") + " btn-success"} onClick={this.props.onClick}>
        <i class="fas fa-search"></i>
      </button>
    );
  }
}

class RandomButton extends Component {
  render() {
    return (
      <button id="randomButton" type="button" class={"btn" + (this.props.large ? " btn-lg" : "") + " btn-secondary shadow"} onClick={this.props.onClick}>Random</button>
    );
  }
}

export default SummonerSearch;
