import React, { Component } from 'react';

export default class Mastery extends Component {
  render() {
    var style = {"font-size": "0.9em"};
    switch(this.props.level) {
      case 5:
        style["background-color"] = "#9b2600"
        break;
      case 6:
        style["background-color"] = "#a1199d"
        break;
      case 7:
        style["background-color"] = "#0ea79d"
        break;
      default:
    }
    var title = "Level " + this.props.level + ": " + this.props.points + " Points";
    return (
      <span class="badge badge-secondary" style={style} title={title}>
        <span class="badge badge-light">{this.props.level}</span>
        <span class="badge">{this.props.points}</span>
      </span>
    );
  }
}
