import React, { Component } from 'react';
import {
  BrowserRouter as Router,
  Redirect,
  Route,
  Switch,
} from 'react-router-dom'

import MapName from '../Map';
import QueueName from '../Queue';
import SummonerName from '../Summoner';

import './app.css';

import get from '../../utils/get.js';

import NavBar from '../navbar';
import LiveGame from '../livegame';

import Home from '../../pages/home';
import Faq from '../../pages/faq';
import About from '../../pages/about';

class App extends Component {
  state = {
    summoner: "",
    region: "",
    game: null,
    error: "",
    controller: new AbortController()
  };

  componentDidMount() {console.log("App mounted", this.state);}
  componentDidUpdate() {console.log("App updated", this.state);}
  componentWillUnmount() {console.log("App unmounted", this.state);}

  handleSearch = () => {
    this.updateSummoner(document.getElementById("summonerName").value);
    console.log("Search clicked: " + this.state.summoner);
  };

  setInfo = (region, summoner) => {
    console.log("updateInfo:", region, summoner);
    if (region !== this.state.region || summoner !== this.state.summoner) {
      this.setState({region: region, summoner: summoner, error: "", game: null});
    } else {
      console.log("DUP: ", region, summoner);
    }
  };

  refresh = (region, summoner) => {
    this.fetchLiveGameInfo(region, summoner);
    this.setState({region: region, summoner: summoner, error: "", game: null});
  }

  render() {
    return (
      <Router basename={process.env.PUBLIC_URL}>
        <div class="d-flex h-100 flex-column">
          <Switch>
            <Route path="/:region(na|euw|eune|kr|br|tr|ru|lan|las|oce|jp)" exact render={(props) =>
              <NavBar {...props}
                region={this.state.region}
                summoner={this.state.summoner}
                refresh={this.refresh}
                controller={this.state.controller}
              />}
            />
            <Route path="/:region(na|euw|eune|kr|br|tr|ru|lan|las|oce|jp)/:summoner"
              render={(props) =>
              <NavBar {...props}
                region={this.state.region}
                summoner={this.state.summoner}
                refresh={this.refresh}
              />}
            />
            <Route render={(props) =>
              <NavBar {...props}
                region={this.state.region}
                summoner={this.state.summoner}
                refresh={this.refresh}
              />}
            />
          </Switch>
          <Switch>
            <Route path="/" exact render={(props) =>
              <Home {...props}
                region={this.state.region}
                summoner={this.state.summoner}
                refresh={this.refresh}
              />}
            />
            <Redirect from="/" exact to="/na" />
            <Route path="/about" exact component={About}/>
            <Route path="/faq" exact component={Faq}/>
            <Route path="/:region(na|euw|eune|kr|br|tr|ru|lan|las|oce|jp)" exact render={(props) =>
              <Home {...props}
                region={this.state.region}
                summoner={this.state.summoner}
                setInfo={this.setInfo}
              />}
            />
            <Route path="/:region(na|euw|eune|kr|br|tr|ru|lan|las|oce|jp)/:summoner"
              render={(props) =>
                <LiveGame {...props}
                  region={this.state.region}
                  summoner={this.state.summoner}
                  error={this.state.error}
                  game={this.state.game}
                  setInfo={this.setInfo}
                  controller={this.state.controller}
                />}
            />
          </Switch>
          <div class="container mt-auto py-3">&copy; 2018{new Date().getFullYear()>2018&&"-"+new Date().getFullYear()} LiveGame.ga</div>
        </div>
      </Router>
    );
  }
}

export default App;
