import React, { Component } from 'react';
import { Route, Link, Switch } from 'react-router-dom';

import SummonerSearch from '../summonersearch';

export default class NavBar extends Component {
  componentDidMount() {console.log("NavBar::componentDidMount", this.state, this.props);}
  componentDidUpdate() {console.log("NavBar::componentDidUpdate", this.state, this.props);}
  componentWillUnmount() {console.log("NavBar::componentWillUnmount", this.state, this.props);}
  render() {
    return (
      <div class="container-fluid p-0 shadow">
        <nav class="navbar navbar-expand-lg navbar-dark bg-dark">
          <div class="container">
            <Link class="navbar-brand" to={"/" + this.props.region}>League of Legends Live Game</Link>
            <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
              <span class="navbar-toggler-icon"></span>
            </button>
            <div class="navbar-nav mr-auto collapse navbar-collapse" id="navbarNav">
              <Switch>
                <Route path={"/" + this.props.region} exact render={(props) => <Link class="nav-item nav-link active" to={"/" + this.props.region}>Home</Link> } />
                <Route render={(props) => <Link class="nav-item nav-link" to={"/" + this.props.region}>Home</Link> } />
              </Switch>
              <Switch>
                <Route path="/about" exact render={(props) => <Link class="nav-item nav-link active" to="/about">About</Link> } />
                <Route render={(props) => <Link class="nav-item nav-link" to="/about">About</Link> } />
              </Switch>
              <Switch>
                <Route path="/faq" exact render={(props) => <Link class="nav-item nav-link active" to="/faq">FAQ</Link> } />
                <Route render={(props) => <Link class="nav-item nav-link" to="/faq">FAQ</Link> } />
              </Switch>
            </div>
            <div class="form-inline">
              <SummonerSearch
                name="<navbar>"
                region={this.props.region}
                summoner={this.props.summoner}
                refresh={this.props.refresh}
                match={this.props.match}
              />
            </div>
          </div>
        </nav>
      </div>
    );
  }
}
