import React, { Component } from 'react';

export default class Timer extends Component {
  constructor(props) {
    super(props);
    console.log("Timer::timestamp: " + this.props.timestamp)
    if(this.props.timestamp > 0) {
      this.state = {
        seconds: Math.floor((Date.now() - this.props.timestamp) / 1000)
      };
    } else {
      this.state = {seconds: 0};
    }
  }

  tick() {
    this.setState(prevState => ({
      seconds: prevState.seconds + 1
    }));
  }

  componentDidMount() {
    this._ismounted = true;
    if(this.state.seconds > 0) {
      this.interval = setInterval(() => this.tick(), 1000);
    }
  }

  componentWillUnmount() {
    this._ismounted = false;
    clearInterval(this.interval);
  }

  render() {
    if(this.state.seconds === 0) {
      return (
        <div>
          <i class="far fa-clock"></i> loading screen...
        </div>
      );
    }
    var hours = Math.floor(this.state.seconds / 3600);
    var minutes = Math.floor(this.state.seconds / 60 - hours * 60);
    if (String(minutes).length < 2) {
      minutes = '0' + minutes;
    }
    var seconds = this.state.seconds - hours * 3600 - minutes * 60;
    if (String(seconds).length < 2) {
      seconds = '0' + seconds;
    }
    return (
      <div>
        <i class="far fa-clock"></i> {hours > 0 && hours + ":"}{minutes}:{seconds}
      </div>
    );
  }
}
