import React from 'react';

function mapName(mapId) {
  var mapping = {}
  mapping[1] = "Summoner's Rift"
  mapping[2] = "Summoner's Rift"
  mapping[3] = "The Proving Grounds"
  mapping[4] = "Twisted Treeline"
  mapping[8] = "The Crystal Scar"
  mapping[10] = "Twisted Treeline"
  mapping[11] = "Summoner's Rift"
  mapping[12] = "Howling Abyss"
  mapping[14] = "Butcher's Bridge"
  mapping[16] = "Cosmic Ruins"
  mapping[18] = "Valoran City Park"
  mapping[19] = "Substructure 43"
  return mapping[mapId] || "<mapId " + mapId + ">";
}

const MapName = ({id}) => <span title={id}>{mapName(id)}</span>

export default MapName;
