import React, { Component } from 'react';

import MapName from '../Map';
import QueueName from '../Queue';
import SummonerName from '../Summoner';
import Timer from '../Timer';
import Mastery from '../Mastery';

import get from '../../utils/get.js';


export default class LiveGame extends Component {
  constructor(props) {
    super(props)

    var region = this.props.match.params.region;
    var summoner = this.props.match.params.summoner;
    console.log("LiveGame constructor: ", region, summoner);
    this.state = {
      region: region,
      summoner: summoner,
      game: null,
      error: "",
      controller: this.props.controller
    };
    this.fetchLiveGameInfo(region, summoner);
  }

  componentDidMount() {
    var region = this.props.match.params.region;
    var summoner = this.props.match.params.summoner;
    console.log("LiveGame mounted: ", region, summoner);
    loadStaticData();
  }

  componentDidUpdate() {
    var region = this.props.match.params.region;
    var summoner = this.props.match.params.summoner;
    console.log("LiveGame updated: ", region, summoner, this.state, this.props);

    if (region !== this.state.region) {
      console.log("region mismatch");
    }
    if (summoner !== this.state.summoner) {
      console.log("summoner mismatch");
    }
    if ((region !== this.state.region) || (summoner !== this.state.summoner)) {
      this.setState({region: region, summoner: summoner, game: null});
      this.fetchLiveGameInfo(region, summoner);
    }
  }

  componentWillUnmount() {
    console.log("LiveGame unmounted: ", this.props.match.params.summoner, this.state);
  }

  fetchChampionStats = (region, summoner, championId) => {
    //Object.assign({},obj1, obj2);
    console.log("getChampionStats", region, summoner, championId)
    var url = "https://www.ocf.berkeley.edu/~omarali/lolapifalcon/" + region + "/summoner/" + summoner + "/stats/champion/" + championId;
    get(url).then(result => {
      console.log("fetchChampionStats:", result);
      switch (result.status) {
        case 200:
          result.json().then(json => {
            console.log("fetchChampionStats", json);
            this.setState((state, props) => {
              var game = state.game;
              if(game === null) return;
              new Map(Object.entries(game.teams)).forEach((players, teamId, teams) => {
                players.forEach((player, index, players) => {
                  if(player.name === summoner) {
                    console.log(json);
                    game.teams[teamId][index] = Object.assign({}, player, {"champStats":json});
                  }
                });
              });
              return {game: game};
            });
          });
          break;
        default:
          this.setState((state, props) => {
            var game = state.game;
            if(game === null) return;
            new Map(Object.entries(game.teams)).forEach((players, teamId, teams) => {
              players.forEach((player, index, players) => {
                if(player.name === summoner) {
                  var json = {"champStats": {"error": url}}
                  game.teams[teamId][index] = Object.assign({}, player, json);
                }
              });
            });
            console.log("omar", game);
            return {game: game};
          });
        }
    }, (error) => {
      this.setState((state, props) => {
        var game = state.game;
        if(game === null) return;
        new Map(Object.entries(game.teams)).forEach((players, teamId, teams) => {
          players.forEach((player, index, players) => {
            if(player.name === summoner) {
              var json = {"champStats": {"error": url}}
              game.teams[teamId][index] = Object.assign({}, player, json);
            }
          });
        });
        return {game: game};
      });
    });
  }

  fetchRanks = (region, summoner) => {
    //Object.assign({},obj1, obj2);
    console.log("getRanks", region, summoner)
    var url = "https://www.ocf.berkeley.edu/~omarali/lolapifalcon/" + region + "/summoner/" + summoner + "/ranks";
    get(url).then(result => {
      console.log("getRanks:", result);
      switch (result.status) {
        case 200:
          result.json().then(json => {
            console.log("fetchRanks", json);
            this.setState((state, props) => {
              var game = state.game;
              if(game === null) return;
              new Map(Object.entries(game.teams)).forEach((players, teamId, teams) => {
                players.forEach((player, index, players) => {
                  if(player.name === summoner) {
                    console.log(json);
                    game.teams[teamId][index] = Object.assign({}, player, {"flexRank": json["RANKED_FLEX_SR"]});
                    game.teams[teamId][index] = Object.assign({}, player, {"soloRank": json["RANKED_SOLO_5x5"]});
                  }
                });
              });
              return { game: game };
            });
          });
          break;
        default:
          this.setState((state, props) => {
            var game = state.game;
            if(game === null) return;
            new Map(Object.entries(game.teams)).forEach((players, teamId, teams) => {
              players.forEach((player, index, players) => {
                if(player.name === summoner) {
                  if(player.soloRank.tier === "Unranked") {
                    var json = {"soloRank": {"error": url, "tier": "Unranked"}}
                    game.teams[teamId][index] = Object.assign({}, player, json);
                  }
                  if(player.flexRank.tier === "Unranked") {
                    var json2 = {"flexRank": {"error": url, "tier": "Unranked"}}
                    game.teams[teamId][index] = Object.assign({}, player, json2);
                  }
                }
              });
            });
            console.log("omar", game);
            return {game: game};
          });
        }
    }, (error) => {
      this.setState((state, props) => {
        var game = state.game;
        if(game === null) return;
        new Map(Object.entries(game.teams)).forEach((players, teamId, teams) => {
          players.forEach((player, index, players) => {
            if(player.name === summoner) {
              if(player.soloRank.tier === "Unranked") {
                var json = {"soloRank": {"error": url, "tier": "Unranked"}}
                game.teams[teamId][index] = Object.assign({}, player, json);
              }
              if(player.flexRank.tier === "Unranked") {
                json = {"flexRank": {"error": url, "tier": "Unranked"}}
                game.teams[teamId][index] = Object.assign({}, player, json);
              }
            }
          });
        });
        return {game: game};
      });
    });
  }

  fetchPreviousRanks = (region, summoner) => {
    //Object.assign({},obj1, obj2);
    console.log("getPreviousRanks", region, summoner);
    var url = "https://www.ocf.berkeley.edu/~omarali/lolapifalcon/" + region + "/summoner/" + summoner + "/previousranks";
    get(url).then(result => {
      console.log("getPreviousRanks:", result);
      switch (result.status) {
        case 200:
          result.json().then(json => {
            console.log("fetchPreviousRanks", json);
            this.setState((state, props) => {
              var game = state.game;
              if(game === null) return;
              new Map(Object.entries(game.teams)).forEach((players, teamId, teams) => {
                players.forEach((player, index, players) => {
                  if(player.name === summoner) {
                    console.log(json);
                    player = Object.assign({}, player, {"previousRanks": json});
                    if(json.RANKED_SOLO_5x5) {
                      player = Object.assign({}, player, {"previousSoloRank": json["RANKED_SOLO_5x5"]});
                    } else {
                      player = Object.assign({}, player, {"previousSoloRank": {"tier": "Unknown"}});
                    }
                    if(json.RANKED_FLEX_SR) {
                      player = Object.assign({}, player, {"previousFlexRank": json["RANKED_FLEX_SR"]});
                    } else {
                      player = Object.assign({}, player, {"previousFlexRank": {"tier": "Unknown"}});
                    }
                    game.teams[teamId][index] = player;
                  }
                });
              });
              return {game: game};
            });
          });
          break;
        default:
          this.setState((state, props) => {
            var game = state.game;
            if(game === null) return;
            new Map(Object.entries(game.teams)).forEach((players, teamId, teams) => {
              players.forEach((player, index, players) => {
                if(player.name === summoner) {
                  var json = {"previousSoloRank": {"error": url}, "previousFlexRank": {"error": url}}
                  game.teams[teamId][index] = Object.assign({}, player, json);
                }
              });
            });
            console.log("omar", game);
            return {game: game};
          });
        }
    }, (error) => {
      this.setState((state, props) => {
        var game = state.game;
        if(game === null) return;
        new Map(Object.entries(game.teams)).forEach((players, teamId, teams) => {
          players.forEach((player, index, players) => {
            if(player.name === summoner) {
              var json = {"previousSoloRank": {"error": url}, "previousFlexRank": {"error": url}}
              game.teams[teamId][index] = Object.assign({}, player, json);
            }
          });
        });
        return {game: game};
      });
    });
  }

  fetchLiveGameInfo = (region, summoner) => {
    console.log("fetchLiveGameInfo(", region, summoner, ")")
    var url = "https://www.ocf.berkeley.edu/~omarali/lolapifalcon/" + region + "/summoner/" + summoner + "/livegame";
    get(url).then(result => {
      console.log("fetchLiveGameInfo:", result);
      switch (result.status) {
        case 200:
          result.json().then(json => {
            console.log(json);
            this.setState({ error: "", game: json, region: region, summoner: summoner });
            // Fetch previous ranks.
            new Map(Object.entries(json.teams)).forEach((players, teamId, teams) => {
              players.forEach(player => {
                //this.fetchPreviousRanks(region, player.name);
              });
            });
            new Map(Object.entries(json.teams)).forEach((players, teamId, teams) => {
              players.forEach(player => {
                //if(player.soloRank.tier === "Unranked" ||
                //   player.flexRank.tier === "Unranked") {
                //  this.fetchRanks(region, player.name);
                //}
                this.fetchChampionStats(region, player.name, player.championId);
              });
            });
          });
          break;
        case 400:
          result.text().then(text => {
            console.log(text);
            this.setState({error: text, game: null});
          });
          break;
        case 404:
          result.text().then(text => {
            console.log(text);
            this.setState({error: text, game:null});
          });
          break;
        case 500:
        default:
          return result.text();
        }
    }, (error) => {
      console.log("ERROR: ", error);
      this.setState({error: error, game:null});
    });
  }

  render() {
    if (this.state.error !== "") {
      console.log("error: " + this.state.error)
      return (
        <>
          <div class="container mt-3">
            <div id="status" class="alert alert-danger">{this.state.error.toString()}</div>
          </div>
        </>
      )
    }

    if (this.state.game === null) {
      return (
        <>
          <div class="container text-center my-auto">
            <div id="loading">
              <i class="fa fa-circle-notch fa-spin fa-3x fa-fw"></i>
              <span class="sr-only">Loading...</span>
              <br />
              Loading... [{this.state.region.toUpperCase()}] {this.state.summoner}
            </div>
          </div>
        </>
      )
    }

    var game = this.state.game;
    return (
      <>
        <div class="container mt-3">
          <div class="row">
            <div class="col-10"><h3><MapName id={game.mapId} /> - <QueueName id={game.queueId} /></h3></div>
            <div class="col-2 text-right pt-auto" id="timer" style={{display: "block"}}><Timer timestamp={game.gameStartTime} /></div>
          </div>
        </div>
        <div class="container-fluid mt-3">
          <table class="table table-sm table-condensed table-striped table-hover thead-dark" style={{"max-width": "1200px", "margin": "auto"}}>
          <Team region={this.props.match.params.region} summoners={game.teams['100']} active={this.props.match.params.summoner} />
          <tr>
            <td colspan="10">
          <div class="row">
            <div class="col-6">
              {game.bans["100"] && game.bans["100"].map(championId =>
                <div class="d-inline mx-1">
                  <Champion id={championId} banned />
                </div>
              )}
            </div>
            <div class="col-6 text-right">
              {game.bans["200"] && game.bans["200"].map(championId =>
                <div class="d-inline mx-1">
                  <Champion id={championId} banned />
                </div>
              )}
            </div>
          </div>
            </td>
          </tr>
          <Team region={this.props.match.params.region} summoners={game.teams['200']} active={this.props.match.params.summoner} />
          </table>
        </div>
      </>
    );
  }
}

class Team extends Component {
  render() {
    return (
      <>
        <thead>
          <tr>
            <th>Name / Level</th>
            <th style={{"text-align":"center"}}>Keystone</th>
            <th style={{"text-align":"center"}}>Spells</th>
            <th style={{"text-align":"center"}}>Champion</th>
            <th style={{"text-align":"center"}}>Solo/Duo</th>
            <th style={{"text-align":"center"}}>Flex 5v5</th>
            <th style={{"text-align":"center","display": "none"}}>S8 Solo</th>
            <th style={{"text-align":"center","display": "none"}}>S8 Flex</th>
          </tr>
        </thead>
        <tbody>
          {this.props.summoners.map(summoner =>
            <tr data-name={summoner.name} data-sid={summoner.summonerId} id={"summoner" + summoner.summonerId}>
              <td style={{"vertical-align":"middle"}}>
                <SummonerName region={this.props.region} name={summoner.name} />
                <br />
                <div class="text-muted" style={{"font-size":".8em"}}>{summoner.level}</div>
              </td>
              <td style={{"vertical-align":"middle","text-align":"center"}}>
                <div class="row no-gutters">
                  <div class="col">
                    <div style={{"display":"inline-block"}}>
                      <Perk id={summoner.perks.perkIds[0]} />
                      <br />
                      <PerkStyle id={summoner.perks.perkSubStyle} />
                    </div>
                  </div>
                </div>
              </td>
              <td style={{"vertical-align":"middle","text-align":"center"}}>
                <div class="row no-gutters">
                  <div class="col" style={{"display":"inline-block"}}>
                    <div class="col">
                      <Spell id={summoner.spell1Id} />
                    </div>
                    <div class="col">
                      <Spell id={summoner.spell2Id} />
                    </div>
                  </div>
                </div>
              </td>
              <td style={{"vertical-align":"middle","text-align":"center"}}>
                <div class="row no-gutters">
                  <div class="d-flex align-self-center justify-content-center">
                    <Champion id={summoner.championId} />
                  </div>
                  <div class="col">
                    {(() => {
                      if(summoner.champStats && "error" in summoner.champStats) {
                        return <Error url={summoner.champStats.error} />
                      } else {
                        return <ChampStats level={summoner.championLevel} points={summoner.championPoints} stats={summoner.champStats} />
                      }
                    })()}
                  </div>
                </div>
              </td>
              <td id="solorank" style={{"vertical-align":"middle","text-align":"center"}}>
                <div class="row no-gutters">
                  <div class="col">
                    <RankImage rank={summoner.ranks.solo} size={36} />
                    { summoner.ranks.solo.tier !== "Unranked" && <>{summoner.ranks.solo.division}<br />{summoner.ranks.solo.points+'LP'}</> }
                  </div>
                  <div class="col">
                    <WinRate wins={summoner.ranks.solo.wins} losses={summoner.ranks.solo.losses} />
                    {summoner.ranks.solo.series && <br />}
                    <Series series={summoner.ranks.solo.series} />
                  </div>
                </div>
              </td>
              <td id="flex5rank" style={{"vertical-align":"middle","text-align":"center"}}>
                <div class="row no-gutters">
                  <div class="col">
                    <RankImage rank={summoner.ranks.flex5} size={36} />
                    { summoner.ranks.flex5.tier !== "Unranked" && <>{summoner.ranks.flex5.division}<br />{summoner.ranks.flex5.points+'LP'}</> }
                  </div>
                  <div class="col">
                    <WinRate wins={summoner.ranks.flex5.wins} losses={summoner.ranks.flex5.losses} />
                    {summoner.ranks.flex5.series && <br />}
                    <Series series={summoner.ranks.flex5.series} />
                  </div>
                </div>
              </td>
              <td id="previoussolo" style={{"vertical-align":"middle","text-align":"center","display":"none"}}>
                {(() => {
                  if(summoner.previousSoloRank && summoner.previousSoloRank.error) {
                    return <Error url={summoner.previousSoloRank.error} />
                  } else {
                    return <RankImage rank={summoner.previousSoloRank} size={24} />
                  }
                })()}
              </td>
              <td id="previousflex" style={{"vertical-align":"middle","text-align":"center","display":"none"}}>
                {(() => {
                  if(summoner.previousFlexRank && summoner.previousFlexRank.error) {
                    return <Error url={summoner.previousFlexRank.error} />
                  } else {
                    return <RankImage rank={summoner.previousFlexRank} size={24} />
                  }
                })()}
              </td>
            </tr>
          )}
        </tbody>
      </>
    );
  }
}

class RankImage extends Component {
  render() {
    if(this.props.rank === undefined) { return <Spinner /> }
    var title = this.props.rank.tier;
    if(this.props.rank.division) { title = title + ' ' + this.props.rank.division }
    if(this.props.rank.points) {title = title + ' ' + this.props.rank.points + ' LP' }
    return (
      <img
        src={"https://www.ocf.berkeley.edu/~omarali/static/lol/ranked/"+this.props.rank.tier.toLowerCase()+".png"}
        height={this.props.size}
        alt={this.props.rank.tier}
        title={title}
      />
    );
  }
}

class ChampStats extends Component {
  render() {
    if(this.props.stats === undefined) { return <Spinner />; }

    var kills = this.props.stats.kills;
    var deaths = this.props.stats.deaths;
    var assists = this.props.stats.assists;

    kills = kills === Math.round(kills) ? kills.toFixed(0) : kills.toFixed(1);
    deaths = deaths === Math.round(deaths) ? deaths.toFixed(0) : deaths.toFixed(1);
    assists = assists === Math.round(assists) ? assists.toFixed(0) : assists.toFixed(1);

    return (
      <>
        <WinRate wins={this.props.stats.wins} losses={this.props.stats.losses} />
        <br />
        <span title="Kills / Deaths / Assists">{kills} / {deaths} / {assists}</span>
        <br />
        <Mastery level={this.props.level} points={this.props.points} />
      </>
    );
  }
}

class WinRate extends Component {
  render() {
    var wins = this.props.wins;
    var losses = this.props.losses;
    var percentage;

    if(wins === undefined) { return <Spinner />; }

    if(wins + losses > 0) {
      var style;
      var rate = (100 * wins / (wins + losses)).toFixed(0);
      if(rate >= 50) {
        style = "success";
      } else if(rate >= 40) {
        style = "warning";
      } else {
        style = "danger";
      }
      percentage = <span class={"text-" + style}> {rate}%</span>;
    } else {
      percentage = null;
    }
    return (
      <span style={{"white-space": "nowrap"}}>
        <span class="text-success">{wins}W</span>
        <span class="text-danger"> {losses}L</span>
        {percentage}
      </span>
    );
  }
}

class Series extends Component {
  format = (result) => {
    switch (result) {
      case "W":
        return <span class="text-success"> </span>;
      case "L":
        return <span class="text-danger"> </span>;
      case "N":
        return <span class="text-muted">- </span>;
      default:
        return null;
    }
  }

  render() {
    if(this.props.series === undefined) { return null; }

    return (
      <>
        {this.format(this.props.series[0])}
        {this.format(this.props.series[1])}
        {this.format(this.props.series[2])}
        {this.format(this.props.series[3])}
        {this.format(this.props.series[4])}
      </>
    );
  }
}

class Spinner extends Component {
  render() {
    return (
      <>
        <i class="fa fa-circle-notch fa-spin fa-1x fa-fw"></i>
        <span class="sr-only">Loading...</span>
      </>
    );
  }
}

class Error extends Component {
  render() {
    var url = this.props.url;
    return (<a href={url}><i class="fas fa-exclamation-triangle text-danger" target="_blank" rel="noopener noreferrer"></i></a>);
  }
}

