function Champion(props) {
  var style = {}
  if(props.banned) {
    style["filter"] = "grayscale(80%)";
  }
  if(props.id > 0) {
    return <img
             src={"//ddragon.leagueoflegends.com/cdn/" + window.version + "/img/champion/" + window.championsById[props.id]["key"] + ".png"}
             alt={window.championsById[props.id]["name"]}
             title={window.championsById[props.id]["name"]}
             style={style}
             height="48"
             class="rounded"
           />
  } else {
    return null;
  }
}

function loadStaticData() {
  var url = "https://ddragon.leagueoflegends.com/api/versions.json"
  fetch(url).then(result => result.json()).then(data => {
    window.version = data[0];
    console.log(window.version);
    loadChampions(window.version);
    loadPerks(window.version);
    loadSummonerSpells(window.version);
  });
}

function loadSummonerSpells(version) {
  var url = "https://ddragon.leagueoflegends.com/cdn/" + version + "/data/en_US/summoner.json";
  fetch(url).then(result => result.json()).then(data => {
    window.summonerSpellsById = {}
    for(var key in data.data) {
      var v = data.data[key]
      window.summonerSpellsById[data.data[key]['key']] = {"name":v.name,"image":v.id};
    }
  });
}

function loadPerks(version) {
  var url = "https://ddragon.leagueoflegends.com/cdn/" + version + "/data/en_US/runesReforged.json";
  fetch(url).then(result => result.json()).then(data => {
    window.perksById = {}
    for(var perk of data) {
      window.perksById[perk.id] = {"name":perk.name,"image":perk.icon};
      for(var slot of perk.slots) {
        for(perk of slot.runes) {
          window.perksById[perk.id] = {"name":perk.name,"image":perk.icon};
        }
      }
    }
  });
}

function loadChampions(version) {
  var url = "https://ddragon.leagueoflegends.com/cdn/" + version + "/data/en_US/champion.json";
  fetch(url).then(result => result.json()).then(data => {
    //console.log(data);
    window.championsById = {}
    //console.log(data.data)
    for(var key in data.data) {
      var v = data.data[key]
      window.championsById[data.data[key]['key']] = {"name":v.name,"key":v.id};
    }
  });
}

